.profile_info {
  color: white;
  text-align: center;
  img {
    margin-bottom: 20px;
    border-radius: 9px;
    object-fit: cover; }
  .info_name {
   margin-bottom: 15px;
   font-size: 25px;
   font-weight: bold;
   text-transform: uppercase; }

  .contact {
    word-wrap: break-word; } }

.button {
  margin-bottom: 20px;
  width: 100%;
  height: 40px;
  font-size: 15px;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid rgb(225, 225, 225);
  color: rgb(225, 225, 225); }
